<template>
  <div
    v-if="open"
    class="mx-auto"
    :class="`${prefixCls}`"
  >
    <div
      class="backdrop"
      @click="handleClose"
    />
    <div
      class="shell overflow-hidden anim-scale-in relative rounded"
      :class="{mobile:isMobile}"
    >
      <slot />
      <a
        @click="handleClose"
        class="block cursor-pointer absolute right-0 p-3"
      >
        <img
          class="close"
          :src="require('@/assets/img/close.png')"
          alt=""
        >
      </a>
    </div>
  </div>
</template>

<script lang="ts">
import { useDesign } from '@/hooks/useDesign';
import { useIsMobile } from '@/hooks/useIsMobile';
import { defineComponent } from 'vue';
export default defineComponent({
  props: {
    open: Boolean,
  },
  emits: ['close'],
  setup(_, { emit }) {
    const { prefixCls } = useDesign('modal');
    const { isMobile } = useIsMobile();
    const handleClose = () => {
      emit('close');
    };
    return { prefixCls, isMobile, handleClose };
  },
});
</script>

