<template>
  <div class="fixed right-2 bottom-0 text-center" style="z-index: 999;">
    <div :class="prefixCls" class="mb-2">
      <div v-for="(item, key) in items" :key="key" :class="`${prefixCls}__item`">
        <!-- @todo todo -->
        <!-- @tag tag -->
        <!-- @note note -->
        <!-- @fix fix -->
        <!-- @done done -->

        <!-- @todo add notification type -->
        <div
          :class="[`${prefixCls}__item--wrap`, `notification-${item.type}`]"
          class="block anim-scale-in bg-white rounded text-gray-470 px-4 py-3 text-xs text-left mb-2"
          v-if="now < item.timestamp + duration"
        >
          {{ item.message }}
          <!-- <UiButton @click="item.hide = true">
            关闭
          </UiButton> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
  const DURATION = 4000;
  import { defineComponent, computed, onMounted, ref } from 'vue';
  import { notifyStore } from '@/store/modules/notifications';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    data() {
      return {
        duration: DURATION,
      };
    },
    setup() {
      const { prefixCls } = useDesign('notification');
      const now = ref(Date.now());
      let timer: any = null;
      const items = computed(() => {
        return notifyStore.getNotifyState;
      });
      onMounted(() => {
        timer = setInterval(() => (now.value = Date.now()), 60);
      });
      return { prefixCls, items, now };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls: ~'@{namespace}-notification';
  .@{prefix-cls} {
    &__item {
      &--wrap {
        min-width: 200px;
        max-width: 300px;
      }
    }
  }
</style>
