
  import { computed, defineComponent, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import { Web3Provider } from '@ethersproject/providers';
  import ClearingHouse from '@/helper/clearingHouse';
  import { BigNumber as BN } from '@ethersproject/bignumber';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import { notifyStore } from '@/store/modules/notifications';
  export default defineComponent({
    name: 'closePosition',
    emits: ['close'],
    data() {
      return {
        slippageList: [0.01, 0.5, 1],
      };
    },
    props: {
      open: {
        type: Boolean,
      },
      address: {
        type: String,
        required: true,
      },
      closePrice: {
        type: String,
        default: '0',
      },
      margin: {
        type: String,
        default: '0',
      },
      unrealized: {
        type: String,
        default: '0',
      },
    },
    setup(props, { emit }) {
      const { prefixCls } = useDesign('close-position');
      const slippage = ref(0.5);
      const handleClose = () => {
        emit('close');
      };
      const loading = computed(() => {
        return loadingBarStore.getLoadingState === LoadingStatus.CLOSE_POSITION;
      });
      function handleClick(val: number) {
        slippage.value = val;
      }
      async function handleClosePosition() {
        try {
          loadingBarStore.setLoading(LoadingStatus.CLOSE_POSITION);
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const tx = await ClearingHouse.closePosition(provider, props.address, BN.from(10));
          await provider.waitForTransaction(tx.hash, 1);
          handleClose();
        } catch (error) {
          if (error.data && error.data.message) {
            notifyStore.commitNotify([error.data.message, 'red']);
          } else {
            console.error(error);
          }
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      }
      return { prefixCls, slippage, handleClose, handleClick, handleClosePosition, loading };
    },
  });
