import { VuexModule, getModule, Module, Mutation, Action } from 'vuex-module-decorators';
import store from '@/store';
export interface NotifyInfo {
  message: string;
  type: string;
  timestamp?: number | undefined;
  hide?: boolean | undefined;
}
const NAME = 'notifications';
@Module({ dynamic: true, namespaced: true, store, name: NAME })
class Notifications extends VuexModule {
  private notifyInfo: NotifyInfo[] = [];

  get getNotifyState(): NotifyInfo[] {
    return this.notifyInfo;
  }

  @Mutation
  notify(info: NotifyInfo) {
    this.notifyInfo.push({ ...info, timestamp: Date.now(), hide: false });
  }

  @Action
  commitNotify(payload: string[] | string) {
    Array.isArray(payload)
      ? this.notify({ message: payload[0], type: payload[1] })
      : this.notify({ message: payload, type: 'green' });
  }
}
export const notifyStore = getModule<Notifications>(Notifications);
